
.general-container {
  width: 100%;
  max-width: 1440px;
  margin: 0 auto;
}

.general-inner {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  margin: 0 auto;
}

.general-name {
  padding: 50px 0;
  color: var(--main-color);
  letter-spacing: 8px;
  text-align: center;
  font-weight: 700;
  text-transform: uppercase;
}

.general-section {
  max-width: 55%;
  display: flex;
  flex-direction: column;
  text-align: center;
  padding: 0 80px;
}

.general-title {
  padding-bottom: 15px;
  font-size: 36px;
  line-height: 48px;
  font-family: 'Banny', sans-serif;
  text-transform: uppercase;
}

.general-subtitle {
  padding: 15px;
  font-size: 18px;
  line-height: 28px;
  font-weight: 700;
}

.general-text {
  font-size: 14px;
  line-height: 24px;
}

.general-block {
  filter: var(--filter-shadow);
  border-radius: 5px;
  display: flex;
  align-items: center;
}

.general-img {
  user-select: none;
}

.helper-border {
  text-align: center;
  width: 60px;
  background: var(--bg-color);
  border: 3px solid var(--bg-color);
  border-radius: 50px;
  margin: auto;
}

.helper-square {
  padding: 100px 100px;
  border: 20px solid var(--bg-color);
  border-radius: 5px;
}

.helper-square--right {
  position: absolute;
  left: 75%;
}

.helper-square--left {
  position: absolute;
  top: 20%;
  right: 75%;
}
