
.nav {
  display: flex;
}

.nav__list {
  display: flex;
  justify-content: center;
}

.nav__list--pr {
  padding-right: 45px;
}

.nav__list--pl {
  padding-left: 35px;
}

.nav__item {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 44px;
  border-bottom: var(--main-border);
}

.nav__link {
  position: relative;
  font-weight: 700;
  line-height: 18px;
  text-decoration: none;
  color: var(--main-color);
  border: 1px solid rgba(0, 0, 0, 0);
}

.nav__link::before {
  position: absolute;
  top: 100%;
  left: 0;
  width: 100%;
  height: 2px;
  background-color: var(--bg-color);
  content: '';
  transform: translateY(8px);
  opacity: 0;
  transition-property: -webkit-transform, opacity;
  transition-duration: 0.3s;
}

.nav__link:hover::before {
  transform: translateY(0);
  opacity: 1;
}

.nav__link:focus {
  outline: none;
  border-bottom: 2px solid var(--bg-color);
}

.nav__picture {
  height: 100px;
  padding-top: 25px;
}

.nav__logo {
  user-select: none;
  padding-bottom: 20px;
}
