.gallery {
  background: #000;
}

.gallery__container {
  width: 100%;
  max-width: 1440px;
  margin: 0 auto;
}

.gallery__photo {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}

.gallery__img {
  width: 320px;
  height: 320px;
  transition: transform 400ms ease 0s;
}

.gallery__img:hover {
  transition: transform 400ms ease 0s;
  transform: scale(1.2, 1.2);
}
