.contact__inner {
  justify-content: center;
}

.contact__section {
  padding: 100px 0 50px;
}

.form__box {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-bottom: 100px;
}

.form__contact {
  padding: 10px 30px 0 15px;
  color: #757575;
  font-weight: 700;
}

.form__text--small {
  width: 33%;
  margin-right: 2px;
}

.form__text--small:last-child {
  margin-right: 0;
}

.form__text--big {
  width: 100%;
  max-height: 120px;
  resize: none;
}

.form__submit--size {
  width: 50%;
}
