
.nav__burger {
  display: none;
  background: none;
  border: 0;
  padding: 0;
  position: absolute;
  right: 0;
  margin: 20px 30px 0 0;
}

.nav__burger-item {
  display: block;
  width: 30px;
  height: 3px;
  background-color: #fff;
  font-size: 0;
  color: transparent;
}

.nav__burger:focus {
  outline: none;
}

.nav__burger-item::before,
.nav__burger-item::after {
  content: "";
  width: 100%;
  height: 100%;
  position: absolute;
  right: 0;
  z-index: 1;
  background-color: #fff;
}

.nav__burger-item::before {
  top: -6px;
}

.nav__burger-item::after {
  bottom: -6px;
}
