
.menu__section {
  color: var(--secondary-color);
}

.menu__inner {
  justify-content: center;
  padding-top: 100px;
}

.menu__list {
  display: flex;
  justify-content: center;
  padding-top: 50px;
}

.menu__item {
  min-width: 120px;
  padding: 15px 0;
  font-size: 12px;
  font-weight: 700;
  text-transform: uppercase;
  line-height: 18px;
  color: var(--secondary-color);
  background: none;
  border: 1px solid transparent;
  border-right: 1px solid rgba(51, 51, 51, 0.2);
  cursor: pointer;
  user-select: none;
}

.menu__item:last-child {
  border-right: 1px solid transparent;
}

.menu__item:focus {
  outline: none;
  border: 1px solid rgba(51, 51, 51, 0.5);
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.5);
}

.accordion {
  display: none;
}

.menu__block {
  width: 100%;
  max-width: 1440px;
  margin: 0 auto;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  padding: 50px 80px 100px;
  color: var(--secondary-color);
}

.menu__card {
  padding-right: 40px;
}

.menu__card:last-child {
  padding-right: 0;
}

.menu__card-item {
  white-space: nowrap;
  padding-bottom: 40px;
}

.menu__card-item:last-child {
  padding-bottom: 0;
}

.menu__card-title {
  font-size: 18px;
  font-family: 'Banny', sans-serif;
}

.menu__card-subtitle {
  font-size: 12px;
  line-height: 18px;
  opacity: 50%;
}
