.events {
  background: linear-gradient(0deg, rgba(0, 0, 0, 0.2), rgba(0, 0, 0, 0.2)),
    url('../../assets/images/events/events-bg.svg') center no-repeat;
  background-size: cover;
  background-attachment: fixed;
}

.events__container {
  display: flex;
  flex-direction: column;
}

.events__inner {
  justify-content: flex-end;
}

.events__section {
  color: var(--main-color);
}

.events__square {
  top: auto;
}

.events__block-img:first-child {
  padding-right: 80px;
}

.events__board {
  height: 100%;
  padding: 20px 34px;
  background-color: var(--secondary-color);
}

.events__board--right {
  position: absolute;
  left: 71%;
}

.events__board--left {
  display: flex;
  position: absolute;
  left: 0;
}

.events__board-text {
  color: var(--main-color);
  font-size: 24px;
  font-family: 'Banny', sans-serif;
  text-transform: uppercase;
  writing-mode: tb;
}

.events__board-text--rotate {
  transform: rotate(-180deg);
}

.events__work-time {
  color: var(--main-color);
  margin: 50px auto;
  opacity: 50%;
}
