
@media (max-width: 1269px) {
  .form__text {
    padding: 10px;
  }

  .form__select {
    padding: 10px;
    text-indent: 0;
  }

  .general-section {
    max-width: 75%;
    padding: 0;
    margin-bottom: 25px;
  }

  .general-img {
    width: 70%;
    padding: 25px 0;
    margin: auto;
  }

  .helper-square {
    border: 15px solid var(--bg-color);
  }

  .helper-square--right {
    left: 67%;
    padding: 60px 60px;
  }

  .helper-square--left {
    top: 33%;
    right: 67%;
    padding: 60px 60px;
  }

  .general-name {
    padding: 25px 0 10px;
  }

  .nav {
    justify-content: center;
  }

  .nav__item {
    width: 90px;
  }

  .about__inner {
    justify-content: center;
    flex-wrap: wrap-reverse;
  }

  .team__inner {
    justify-content: center;
  }

  .specialties__inner {
    justify-content: center;
  }

  .book__inner {
    justify-content: center;
    flex-wrap: wrap-reverse;
  }

  .book__work-time {
    margin: 25px 0 10px;
  }

  .book__form {
    padding-left: 10px;
  }

  .menu__inner {
    padding-top: 25px;
  }

  .menu__list {
    padding-top: 25px;
  }

  .menu__block {
    padding: 50px 40px 75px;
    justify-content: center;
  }

  .menu__card-title {
    font-size: 12px;
  }

  .menu__card-subtitle {
    font-size: 12px;
  }

  .events__inner {
    justify-content: center;
  }

  .events__square {
    top: auto;
  }

  .events__name {
    padding-bottom: 25px;
  }

  .events__block-img:first-child {
    padding: 0;
  }

  .events__img {
    padding: 0;
  }

  .events__board--right {
    left: 68%;
  }
  
  .events__board--left {
    left: 15%;
  }

  .gallery__img {
    width: 340px;
    height: 340px;
    margin-bottom: 10px;
  }

  .gallery__img:first-child {
    margin-right: 10px;
  }

  .gallery__img:last-child {
    margin-left: 10px;
  }

  .form__text--small {
    margin-right: 0;
  }
}

@media (max-width: 956px) {
  .nav__item {
    width: 80px;
  }

  .nav__burger {
    margin: 50px 50px 0 0;
  }
}

@media (max-width: 927px) {
  .nav {
    justify-content: start;
    border-bottom: var(--main-border);
  }

  .nav__list {
    display: none;
  }

  .nav__picture {
    margin: auto;
  }

  .nav__burger {
    display: block;
  }

  .menu__list {
    display: none;
  }

  .menu__block {
    display: none;
  }

  .accordion {
    display: block;
  }

  .menu__card-title {
    font-size: 12px;
  }

  .events__block-img:last-child {
    margin-top: 20px;
  }
}

@media (max-width: 823px) {
  .header__section {
    justify-content: center;
  }

  .header__time {
    display: none;
  }

  .header__social {
    display: none;
  }

  .header__main {
    max-width: 320px;
  }

  .header__title {
    font-size: 70px;
  }

  .header__buttons {
    flex-direction: column;
    align-items: center;
  }

  .header__btn {
    margin: 30px 0 0 0;
  }

  .header__btn:first-child {
    margin-top: 0;
  }

  .header__down-btn {
    margin-top: 50px;
  }
}

@media (max-width: 450px) {
  .book__form {
    width: auto;
  }

  .menu__inner {
    padding-top: 40px;
  }

  .general-section {
    max-width: 100%;
    padding: 0 50px 50px;
  }

  .general-img {
    width: 50%;
    padding: 25px 0;
    margin: auto;
  }

  .helper-square {
    border: 10px solid var(--bg-color);
  }

  .helper-square--right {
    left: 62%;
    padding: 40px 40px;
  }

  .helper-square--left {
    right: 62%;
    padding: 40px 40px;
  }

  .events__img {
    padding: 0;
  }

  .events__board {
    padding: 5px 10px;
  }

  .events__board--left {
    left: 24%;
  }

  .events__board--right {
    left: 67%;
  }

  .events__board-text {
    font-size: 12px;
  }

  .events__work-time {
    text-align: center;
    margin: 40px 20px;
  }
  
  .gallery__img {
    width: 320px;
    height: 320px;
    margin-bottom: 10px;
  }

  .gallery__img:first-child {
    margin-right: 0;
  }
  
  .gallery__img:last-child {
    margin-left: 0;
  }

  .gallery__img:hover {
    transform: none;
  }
  
  .form__text--small {
    width: 32%;
    margin-right: 2px;
  }
}
