
.header__inner {
  background: linear-gradient(0deg, rgba(0, 0, 0, 0.2), rgba(0, 0, 0, 0.2)), url("../../assets/images/header/header-bg.svg") center no-repeat;
  background-size: cover;
  background-attachment: fixed;
}

.header__section {
  max-height: 100%;
  display: flex;
  justify-content: space-between;
}

.header__main {
  display: flex;
  flex-direction: column;
  margin: auto;
  padding: 225px 0 80px 0;
}

.header__subtitle {
  font-size: 12px;
  color: var(--main-color);
  letter-spacing: 0.5rem;
  text-align: center;
}

.header__title {
  padding: 30px 0 30px;
  text-align: center;
  font-family: 'Banny', sans-serif;
  font-size: 96px;
  color: var(--main-color);
  line-height: 80px;
}

.header__buttons {
  display: flex;
  justify-content: center;
  padding-top: 30px;
}

.header__btn {
  padding: 22px 72px;
  margin-left: 40px;
  border: var(--main-border);
  border-radius: 5px;
  background: none;
  font-weight: 700;
  color: var(--main-color);
  display: flex;
  justify-content: center;
  align-items: center;
  transition: background-color 0.7s;
  white-space: nowrap;
  cursor: pointer;
}

.header__btn:first-child {
  padding: 22px 61px;
  margin-left: 0;
}

.header__btn:hover {
  background-color: var(--bg-color);
}

.header__btn:focus {
  outline: none;
  background-color: var(--bg-color);
}

.header__down-btn {
  background: none;
  margin: 85px auto 0;
  width: 60px;
  height: 60px;
  border: var(--main-border);
  border-radius: 50px;
  user-select: none;
  cursor: pointer;
  position: relative;
  animation: span-3 3s ease-in-out infinite;
  transition: border 0.7s;
}

.header__down-btn:hover {
  outline: none;
  border: 1px solid rgba(255, 255, 255, 0.7);
}

.header__down-btn:focus {
  outline: none;
  border: 1px solid rgba(255, 255, 255, 0.7);
}

@keyframes span-3 {
  0% {
    transform: translate(0);
  }

  50% {
    transform: translate(0, -50px);
  }

  100% {
    transform: translate(0);
  }
}

.down-btn {
  width: 40px;
  height: 40px;
  margin: 8px;
  padding: 10px;
  background-color: var(--main-color);
  border: var(--main-border);
  border-radius: 50px;
}

.header__time {
  width: 100%;
  max-width: 215px;
  border-right: var(--main-border);
  display: flex;
  align-items: center;
  justify-content: center;
}

.header__work-time {
  font-weight: 700;
  color: var(--main-color);
  white-space: nowrap;
  writing-mode: tb;
  transform: rotate(-180deg);
}

.header__social {
  width: 100%;
  max-width: 215px;
  border-left: var(--main-border);
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

.social__link {
  text-align: center;
  height: 60px;
  width: 60px;
  padding: 20px;
  margin-bottom: 20px;
  border: var(--main-border);
  border-radius: 50px;
  transition: background-color 0.7s;
}

.social__link:last-child {
  margin-bottom: 0;
}

.social__link:hover {
  background-color: var(--bg-color);
}

.social__link:focus {
  outline: none;
  border: 1px solid #fff;
  border-radius: 50px;
}

.social__icon {
  user-select: none;
}
