
.book__title {
  color: var(--secondary-color);
}

.book__form {
  width: 560px;
  padding-top: 20px;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
}

.book__img {
  padding: 100px 0 0;
}

.form__text {
  width: 47%;
  padding: 20px;
  margin-top: 10px;
  font-size: 14px;
  font-family: 'Open-sans', sans-serif;
  background: #e5e5e5;
  border: 1px solid #e5e5e5;
  border-radius: 5px;
}

.form__text:focus {
  outline: none;
  border: 1px solid rgba(51, 51, 51, 0.5);
}

.form__text:focus::placeholder {
  color: #000;
}

.form__text:first-child {
  margin-top: 0;
}

.form__text:nth-child(2) {
  margin-top: 0;
}

.form__text--mr {
  margin-right: 10px;
}

.form__select {
  width: 47%;
  padding: 20px 0;
  margin-top: 10px;
  background: #e5e5e5;
  border: 1px solid transparent;
  border-radius: 5px;
  font-size: 14px;
  font-family: inherit;
  font-weight: 400;
  text-indent: 16px;
  cursor: pointer;
  color: rgba(51, 51, 51, 0.7);
}

.form__select:focus {
  outline: none;
  border: 1px solid rgba(51, 51, 51, 0.5);
  color: #000;
}

.form__submit {
  width: 96%;
  padding: 20px;
  margin-top: 10px;
  font-size: 12px;
  font-weight: 700;
  text-transform: uppercase;
  color: var(--main-color);
  background: var(--bg-color);
  border: 1px solid var(--bg-color);
  border-radius: 5px;
  transition: background-color 0.7s;
  cursor: pointer;
}

.form__submit:hover {
  color: #000;
  border: 1px solid var(--secondary-color);
  background-color: rgba(255, 255, 255, 0.01);
}

.form__submit:focus {
  outline: none;
  border: 1px solid rgba(51, 51, 51, 0.5);
}

.book__work-time {
  color: var(--secondary-color);
  margin: 50px auto;
  opacity: 50%;
}

